import React, { useState } from "react";
import { Route, Switch } from "react-router-dom";
import Header from "../../shared/Header";
// import CoursesLine from "../../shared/CoursesLine";
import Footer from "../../shared/Footer";
import LandingDrawer from "../../components/LandingDrawer";
import { useMediaQuery, useTheme } from "@mui/material";
import NotFound from "../../pages/NotFound";
import PaymentForServices from "../../pages/dashboard/PaymentForServices";
import { lazy } from "react";
import Services from "../../pages/dashboard/PaymentForServices/Services";
import License from "../../pages/License";
import Details from "../../pages/FAQ/Details";
// import Trade from "../../pages/dashboard/Trade";
// import Alisher from "../../pages/Alisher";
import ForLegalPersons from "../../pages/ForLegalPersons";
import NFT from "../../pages/NFT";
const Home = lazy(() => import("../../pages/Home"));
const About = lazy(() => import("../../pages/About"));
// const Abou = lazy(() => import("../../pages/aboutt"));
const FAQ = lazy(() => import("../../pages/FAQ"));
const News = lazy(() => import("../../pages/News"));
const DetailNews = lazy(() => import("../../pages/News/Detail"));
const Contacts = lazy(() => import("../../pages/Contacts"));
// const HowToStart = lazy(() => import("../../pages/HowToStart"));
const Reviews = lazy(() => import("../../pages/Reviews"));
const TermsOfUse = lazy(() => import("../../pages/TermsOfUse"));
const PrivacyPolicy = lazy(() => import("../../pages/PrivacyPolicy"));
const RefundPolicy = lazy(() => import("../../pages/RefundPolicy"));
const PaymentFSDetails = lazy(() =>
  import("../../pages/dashboard/PaymentForServices/Details")
);

const SiteRoute = ({ t, i18n, colorMode, mode }) => {
  const theme = useTheme();
  const tablet = useMediaQuery(theme.breakpoints.down(1168));
  const [loading, setLoading] = useState(false);

  return (
    <>
      <Header t={t} i18n={i18n} colorMode={colorMode} />
      <main className="main" style={{ paddingTop: 80 }}>
        {/* <CoursesLine /> */}
        <Switch>
          <Route
            exact
            path="/main/home"
            component={() => <Home t={t} i18n={i18n} mode={mode} />}
          />
          <Route exact path="/main/about/" component={() => <About t={t} />} />

          <Route
            exact
            path="/main/license/"
            component={() => <License t={t} />}
          />
          <Route
            exact
            path="/main/payment-for-services/binance-pay/"
            component={() => <PaymentForServices mode={mode} main t={t} />}
          />
          <Route
            exact
            path="/main/payment-for-services/binance-pay/:servicesId"
            component={() => (
              <Services
                setLoading={setLoading}
                mode={mode}
                main
                loading={loading}
                t={t}
              />
            )}
          />
          <Route
            exact
            path="/main/payment-for-services/binance-pay/:servicesId/:detailsId"
            component={() => (
              <PaymentFSDetails
                main
                mode={mode}
                setLoading={setLoading}
                loading={loading}
                t={t}
              />
            )}
          />
          <Route exact path="/main/nft/" component={() => <NFT t={t} />} />
          <Route exact path="/main/FAQ" component={() => <FAQ t={t} />} />
          <Route
            exact
            path="/main/FAQ/:id"
            component={() => <Details t={t} />}
          />
          <Route exact path="/main/news" component={() => <News t={t} />} />
          <Route
            exact
            path="/main/news/:id"
            component={() => <DetailNews t={t} />}
          />
          <Route
            exact
            path="/main/contacts"
            component={() => <Contacts t={t} />}
          />
          {/* <Route
            exact
            path="/main/how-to-start"
            component={() => <HowToStart t={t} />}
          /> */}
          <Route
            exact
            path="/main/reviews"
            component={() => <Reviews mode={mode} t={t} />}
          />
          <Route
            exact
            path="/main/terms-of-use"
            component={() => <TermsOfUse t={t} i18n={i18n} />}
          />
          <Route
            exact
            path="/main/privacy-policy"
            component={() => <PrivacyPolicy t={t} i18n={i18n} />}
          />
          <Route
            exact
            path="/main/refund-policy"
            component={() => <RefundPolicy t={t} i18n={i18n} />}
          />
          <Route
            exact
            path="/main/legal-persons"
            component={() => <ForLegalPersons t={t} i18n={i18n} />}
          />
          {/* <Route
            exact
            path="/main/partners"
            component={() => <Alisher t={t} i18n={i18n} />}
          /> */}
          <Route path="*">
            <NotFound />
          </Route>
        </Switch>
      </main>
      <Footer t={t} />
      {tablet && <LandingDrawer t={t} i18n={i18n} />}
    </>
  );
};

export default SiteRoute;
