import { Box, Grid, TableRow, Typography } from "@mui/material";
import { motion } from "framer-motion/dist/framer-motion";

export const MBox = motion(Box);
export const MGrid = motion(Grid);
export const MTableRow = motion(TableRow);
export const MTypography = motion(Typography);

export const blocksAnimation = {
  hidden: (custom) => ({
    x:
      custom === 1
        ? 120
        : custom === 2
        ? "-120px"
        : custom === 3
        ? "120px"
        : -120,
    y:
      custom === 1
        ? 120
        : custom === 2
        ? "120px"
        : custom === 3
        ? "-120px"
        : -120,
    opacity: 0,
    scale: 0.8,
  }),
  visible: (custom) => ({
    x: 0,
    y: 0,
    opacity: 1,
    scale: 0.8,
    transition: {
      delay: custom * 0.2,
      type: "spring",
      stiffness: 260,
      damping: 10,
    },
  }),
};

