import {
  Accordion,
  AccordionDetails,
  AccordionSummary,
  Button,
  Container,
  Paper,
  Typography,
} from "@mui/material";
import React, { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useHistory, useLocation } from "react-router";
// import { Swiper, SwiperSlide } from "swiper/react";
// import { motion } from "framer-motion/dist/framer-motion";
import { getFaq } from "../../redux/reducers/questionsReducer";

const Details = ({ t }) => {
  const location = useLocation();
  const dispatch = useDispatch();
  const history = useHistory();

  const { faq } = useSelector(({ questionReducer }) => questionReducer);

  const category = location.pathname.split("/")[3];

  const arryFaq = Object.values(typeof faq.faq === "object" && faq.faq).map(
    (i) => i
  );

  const [expanded, setExpanded] = React.useState(false);
  const handleChangeAcordion = (panel) => (event, isExpanded) => {
    setExpanded(isExpanded ? panel : false);
  };

  useEffect(() => {
    dispatch(getFaq());
    // eslint-disable-next-line
  }, []);

  return (
    <Paper sx={{ p: "80px 0" }} component="section">
      <Container>
        <Button
          sx={{ mb: 2 }}
          color="secondary"
          onClick={() => history.goBack()}
          size="medium"
        >
          <svg
            style={{
              marginRight: 16,
            }}
            width="36"
            height="36"
            viewBox="0 0 36 36"
            fill="none"
            xmlns="http://www.w3.org/2000/svg"
          >
            <path
              d="M14.355 8.89478L5.25 17.9998L14.355 27.1048"
              stroke="#292D32"
              stroke-width="1.5"
              stroke-miterlimit="10"
              stroke-linecap="round"
              stroke-linejoin="round"
            />
            <path
              d="M30.7501 18H5.50513"
              stroke="#292D32"
              stroke-width="1.5"
              stroke-miterlimit="10"
              stroke-linecap="round"
              stroke-linejoin="round"
            />
          </svg>

          {t("GoBack")}
        </Button>
        {arryFaq.map((item) =>
          item.map(
            (i, idx) =>
              i.name === category && (
                <Accordion
                  sx={{
                    borderRadius: "13px!important",
                    boxShadow: "0px 1px 0px #E2E8F0",
                    backgroundColor: "#FFF",
                    mb: 2,
                    transition: "all 0.2s linear",

                    "&:hover": {
                      boxShadow: "0px 0px 6px 2px #E2E8F0",
                    },

                    "&:before": {
                      display: "none",
                    },
                  }}
                  key={idx}
                  expanded={expanded === i}
                  onChange={handleChangeAcordion(i)}
                >
                  <AccordionSummary
                    expandIcon={
                      <span
                        style={{
                          width: 0,
                          height: 0,
                          borderLeft: "8px solid transparent",
                          borderRight: "8px solid transparent",
                          borderTop: "10px solid #FCE34D",
                          borderRadius: 3,
                        }}
                      ></span>
                    }
                  >
                    <Typography fontWeight={600} variant="h4">
                      {i.question}
                    </Typography>
                  </AccordionSummary>
                  <AccordionDetails>
                    <Typography
                      variant="h6"
                      fontWeight={300}
                      lineHeight="27px"
                      mb={2}
                      dangerouslySetInnerHTML={{
                        __html: i.answer,
                      }}
                    />
                    {/* <Swiper freeMode={true}>
                      <SwiperSlide>
                        <img
                          data-fancybox="gallery"
                          data-src={item.image}
                          src="https://petapixel.com/assets/uploads/2022/07/DALLEcopy.jpg"
                          width="100%"
                          style={{ objectFit: "cover" }}
                          height="500px"
                          alt=""
                        />
                      </SwiperSlide>
                      <SwiperSlide>
                        <img
                          data-fancybox="gallery"
                          data-src={item.image}
                          src="https://petapixel.com/assets/uploads/2022/07/DALLEcopy.jpg"
                          width="100%"
                          style={{ objectFit: "cover" }}
                          height="500px"
                          alt=""
                        />
                      </SwiperSlide>
                    </Swiper> */}
                  </AccordionDetails>
                </Accordion>
              )
          )
        )}
      </Container>
    </Paper>
  );
};

export default Details;
